<template>
  <div class="gdt">
    <img
      :src="require('@/assets/zeroLive/topBg199.jpg')"
      alt=""
      class="part1" />
    <div class="part6">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请输入手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix"> +86 <span class="icon">▼</span> </span>
          </template>
        </VanField>
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <p
          class="zero__state mt__20 mb__20"
          v-show="stateIsShow"
          @click="stateModelShow=true">《个人信息授权与保护声明》</p>
        <div class="subBtn" @click="submit">199元抢购</div>
      </VanForm>
    </div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>报名成功</p>
          <p>{{ el.time }}秒前</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <!-- 底部图片 -->
    <img
      :class="['zero__img-auto', { mt__40: !index }]"
      v-for="(item, index) in detailImg"
      :key="index"
      :src="require(`@/assets/zeroLive/${item}.png`)" />
    <div class="zero__footer">
      <div class="zero__footer__topic">
        <span class="zero__footer__topic__title">市场价:<span class="price">199</span>元</span>
      </div>
      <div class="zero__footer__btn" @click="submit"><span>立即报名</span></div>
    </div>
    <!-- <BackDiaLog
      @confirm="pay"
      @cancel="stop"
      v-model="showDialog" /> -->
    <Statement
      v-if="stateModelShow"
      @onCloseMask="stateModelShow=false" />
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
  </div>
</template>
<script>
import { getSearch, calArray } from '@/utils'
import { originalConfig } from './config'
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import { codeMap } from './ErrorMsg'
import Statement from '@/components/Statement'
export default {
  components: {
    PayTypeDialog,
    CountDown,
    Statement
  },
  data: function () {
    return {
      detailImg: [1, 2, 3, 4, '6c'],
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      swipeList: [],
      form: {},
      isFinsh: true,
      showPaySelet: false,
      // showDialog: false,
      ...originalConfig
    }
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('ZeroLive199-phone'),
      code: localStorage.getItem('ZeroLive199-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      // this.showDialog = true
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
    // 获取最新报名客户
    const res = await this.$apis.userOperator.getLatestUser()
    this.swipeList = calArray(res, 2)
  },

  beforeDestroy () {
    clearInterval(this.timer)
  },

  methods: {
    // stop () {
    //   clearInterval(this.timer)
    // },
    // 提交表单
    submit () {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0245'
      // })
      this.$apis.userOperator.report()
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        // this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query,
          phone: this.form.phone
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      const medId = getSearch('medId')
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          // this.showDialog = false
          if (code === 30006) {
            if (medId === '3610') {
              window._shTrackSendLog({ trackId: '19', trackType: 'PURCHASE', convertId: '9160' })
            }
            return this.goSucccess()
          }
          return this.showMsg(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0136'
      // })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('ZeroLive199-phone', this.form.phone)
      localStorage.setItem('ZeroLive199-code', this.form.code)
      this.$apis.payment
        .payBeforeCheck({
          ...this.form,
          product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
        })
        .then(async (res) => {
          this.showPaySelet = true
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      // 199暂无挽留弹窗
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>

@keyframes btnScale {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.subBtn {
  height: 84px;
  line-height: 84px;
  text-align: center;
  width: 600px;
  font-size: 48px;
  font-weight: 400;
  color: #fff;
  background: linear-gradient(180deg, #ff7754 0%, #fa501e 100%);
  animation: btnScale 1s infinite;
}

.zero__footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 130px;
  background: #fff;
  display: flex;
  border-radius: 20px 20px 0 0;

  .zero__footer__topic {
    width: 53%;

    @include horVerCenter;

    padding: 0 25px;
    justify-content: space-between;
    font-size: 36px;
    border-radius: 20px 0 0 0;

    .zero__footer__topic__title {
      text-align: center;
      font-size: 36px;
      font-family: PingFang SC;
      font-weight: 400;
      font-style: italic;
      color: #fe3e11;

      .price {
        font-size: 90px;
        font-family: DIN;
        font-weight: bold;
      }
    }

    .zero__footer__topic__number {
      @include horVerCenter;

      width: 200px;
      height: 48px;
      font-size: 38px;
      color: #fff;
      font-style: italic;
      background: #fd5b2d;
      border-radius: 24px;
    }

    .zero__footer__topic__money {
      font-style: italic;
      color: #fe3e11;

      & > i {
        font-size: 112px;
        color: #fe3e11;
        font-weight: bold;
      }
    }
  }

  .zero__footer__btn {
    @include horVerCenter;

    width: 60%;
    height: 100%;
    background: linear-gradient(180deg, #ff7754 0%, #fa501e 100%);
    font-size: 48px;
    font-weight: 600;
    color: #fff;
    border-radius: 0 20px 0 0;

    span {
      animation: btnScale 1s infinite;
    }
  }
}

.gdt {
  padding-bottom: 106px;

  .zero__line-title {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
  }

  .zero__swipe-box {
    margin-left: 72px;
    height: 100px;
    margin-top: 30px;

    .zero__swipe-box__item {
      width: calc(100% - 100px);
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      font-weight: 400;
      line-height: 50px;
      color: #434c5d;
    }
  }

  .footer {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 106px;

    p {
      line-height: 106px;
    }
  }

  .baoming {
    display: block;
    font-family: PingFangSC-Regular;
    font-size: 23px;
    color: #fff;
    line-height: 0.84rem;
    margin: 0 auto;
    text-align: center;
    text-decoration: none;
    background: -webkit-linear-gradient(top, #ff929d, #ff0034);
    background: -o-linear-gradient(bottom, #ff929d, #ff0034);
    background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
    background: linear-gradient(to bottom, #ff929d, #ff0034);
    margin-bottom: 0.4rem;
  }

  .footer div {
    width: 40%;
    height: 100%;
    background-color: #fff;
    box-sizing: border-box;
  }

  .minge,
  .person {
    font-family: PingFangSC-Regular;
    font-size: 0.24rem;
    color: #333;
    text-align: center;
  }

  .person span {
    font-family: PingFangSC-Semibold;
    font-size: 0.4rem;
    color: #ff2c12;
    text-align: center;
  }

  .footer a {
    /* float: right; */
    display: block;
    width: 60%;
    height: 100%;
    font-family: PingFangSC-Semibold;
    font-size: 46px;
    color: #fffcff;
    line-height: 106px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    background: -webkit-linear-gradient(top, #ff929d, #ff0034);
    background: -o-linear-gradient(bottom, #ff929d, #ff0034);
    background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
    background: linear-gradient(to bottom, #ff929d, #ff0034);
  }

  img {
    width: 100%;
    vertical-align: top;
  }

  .part6 {
    // height: 380px;
    box-sizing: border-box;
    padding: 80px 75px;
    text-align: center;

    .tuikuan {
      font-family: PingFangSC-Medium;
      font-size: 26px;
      color: #38465d;
      margin-top: 38px;
      margin-bottom: 38px;
      text-align: center;
    }

    .wrap {
      position: relative;
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      // border-left: rgba(0, 0, 0, 0.2) solid 1px;
      font-size: 32px;
      font-weight: 500;
      line-height: 32px;
      font-family: PingFangSC-Regular;
      color: #8b8c8d;
    }

    /deep/ .van-cell {
      .prefix {
        font-size: 32px;
        font-weight: 500;
        color: #000;
        display: flex;
        align-items: center;

        .icon {
          font-size: 25px;
          margin-left: 10px;
          color: #ccc;
        }

        padding-right: 15px;
      }

      margin-bottom: 20px;
      width: 600px;
      height: 86px;
      background: #f5f8ff;
      border-radius: 3px;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }
}
</style>
